.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

button {
  display: flex;
  width: 100%;
  cursor: pointer;
  font-size: 20px;
  font-weight: 500;
  color: blue;
  padding: 0;
  border: none;
  background-color: transparent;
}

button:hover,
button:focus {
  color: orange;
}

.mainList {
  margin: 20px;
}

.backButton {
  margin: 20px;
}
